.lb-loader, .lightbox {
  text-align: center;
  line-height: 0;
  position: absolute;
  left: 0;
}

body.lb-disable-scrolling {
  overflow: hidden;
}

.lightboxOverlay {
  z-index: 9999;
  filter: alpha(Opacity= 80);
  opacity: .8;
  background-color: #000;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.lightbox {
  width: 100%;
  z-index: 10000;
  outline: 0;
  font-weight: 400;
}

.lightbox .lb-image {
  height: auto;
  max-width: inherit;
  max-height: none;
  border: 4px solid #fff;
  border-radius: 3px;
  display: block;
}

.lightbox a img {
  border: none;
}

.lb-outerContainer {
  width: 250px;
  height: 250px;
  background-color: #fff;
  border-radius: 4px;
  margin: 0 auto;
  position: relative;
}

.lb-outerContainer:after {
  content: "";
  clear: both;
  display: table;
}

.lb-loader {
  height: 25%;
  width: 100%;
  top: 43%;
}

.lb-cancel {
  width: 32px;
  height: 32px;
  background: url("loading.46e1ff58.gif") no-repeat;
  margin: 0 auto;
  display: block;
}

.lb-nav {
  height: 100%;
  width: 100%;
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
}

.lb-container > .nav {
  left: 0;
