.team .container {
  padding: 0 32px;
}

.team .overtitle {
  margin-bottom: 24px;
}

.team .text {
  margin: 24px 0;
}

.team-list {
  display: grid;
  gap: 18px;
  grid-template-columns: repeat(3, 1fr);
}

.team-list li:not(:last-child) {
  margin-bottom: 0;
}

.team .name {
  margin-top: 14px;
  font-size: 25px;
  line-height: calc(32 / 25);
}

.team .position {
  font-size: 18px;
  line-height: calc(28 / 18);
}

.thumb,
.slide {
  height: 148px;
}
